import { Badge, Container, Grid, Menu, MenuItem } from "@material-ui/core";
import AppBar from "@material-ui/core/AppBar";
import CssBaseline from "@material-ui/core/CssBaseline";
import Divider from "@material-ui/core/Divider";
import Drawer from "@material-ui/core/Drawer";
import IconButton from "@material-ui/core/IconButton";
import List from "@material-ui/core/List";
import ListItem from "@material-ui/core/ListItem";
import ListItemIcon from "@material-ui/core/ListItemIcon";
import ListItemText from "@material-ui/core/ListItemText";
import {
  createStyles,
  makeStyles,
  Theme,
  useTheme,
} from "@material-ui/core/styles";
import Toolbar from "@material-ui/core/Toolbar";
import Typography from "@material-ui/core/Typography";
import AccountCircle from "@material-ui/icons/AccountCircle";
import ChevronLeftIcon from "@material-ui/icons/ChevronLeft";
import ChevronRightIcon from "@material-ui/icons/ChevronRight";
import MailIcon from "@material-ui/icons/Mail";
import MenuIcon from "@material-ui/icons/Menu";
import MoreIcon from "@material-ui/icons/MoreVert";
import NotificationsIcon from "@material-ui/icons/Notifications";
import clsx from "clsx";
import React from "react";
import { useGoogleLogin, useGoogleLogout } from "react-google-login";
import { connect } from "react-redux";
import { Route, Switch, useHistory } from "react-router-dom";
import { setUser, setSnackBar } from "../redux/actions";
import dashboardRoutes from "../routes";
import AvatarComponent from "./Profile/AvatarComponent";
import MyAccount from "./Profile/MyAccount";
import ProfileComponent from "./Profile/ProfileComponent";
import Reporting from "./Reporting";

const drawerWidth = 240;

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    root: {
      display: "flex",
    },
    grow: {
      flexGrow: 1,
    },
    sectionDesktop: {
      display: "none",
      [theme.breakpoints.up("md")]: {
        display: "flex",
      },
    },
    appBar: {
      zIndex: theme.zIndex.drawer + 1,
      transition: theme.transitions.create(["width", "margin"], {
        easing: theme.transitions.easing.sharp,
        duration: theme.transitions.duration.leavingScreen,
      }),
    },
    sectionMobile: {
      display: "flex",
      [theme.breakpoints.up("md")]: {
        display: "none",
      },
    },
    appBarShift: {
      marginLeft: drawerWidth,
      width: `calc(100% - ${drawerWidth}px)`,
      transition: theme.transitions.create(["width", "margin"], {
        easing: theme.transitions.easing.sharp,
        duration: theme.transitions.duration.enteringScreen,
      }),
    },
    menuButton: {
      marginRight: 36,
    },
    hide: {
      display: "none",
    },
    drawer: {
      width: drawerWidth,
      flexShrink: 0,
      whiteSpace: "nowrap",
    },
    drawerOpen: {
      width: drawerWidth,
      transition: theme.transitions.create("width", {
        easing: theme.transitions.easing.sharp,
        duration: theme.transitions.duration.enteringScreen,
      }),
    },
    drawerClose: {
      transition: theme.transitions.create("width", {
        easing: theme.transitions.easing.sharp,
        duration: theme.transitions.duration.leavingScreen,
      }),
      overflowX: "hidden",
      width: theme.spacing(7) + 1,
      [theme.breakpoints.up("sm")]: {
        width: theme.spacing(9) + 1,
      },
    },
    toolbar: {
      display: "flex",
      alignItems: "center",
      justifyContent: "flex-end",
      padding: theme.spacing(0, 1),
      // necessary for content to be below app bar
      ...theme.mixins.toolbar,
    },
    content: {
      flexGrow: 1,
      padding: theme.spacing(3),
    },
    list: {
      width: 250,
    },
    fullList: {
      width: "auto",
    },
  })
);

function MiniDrawer(props: any) {
  const classes = useStyles();
  const history = useHistory();
  const theme = useTheme();
  const [open, setOpen] = React.useState(false);
  const [state, setState] = React.useState({
    left: false,
  });
  const [anchorEl, setAnchorEl] = React.useState<null | HTMLElement>(null);
  const [
    mobileMoreAnchorEl,
    setMobileMoreAnchorEl,
  ] = React.useState<null | HTMLElement>(null);

  const handleDrawerOpen = () => {
    setOpen(true);
  };
  const linkClick = (link: string) => {
    history.push(link);
    setOpen(false);
  };
  const handleDrawerClose = () => {
    setOpen(false);
  };
  const menuId = "primary-search-account-menu";
  const handleProfileMenuOpen = (event: React.MouseEvent<HTMLElement>) => {
    setAnchorEl(event.currentTarget);
  };

  const handleMobileMenuClose = () => {
    setMobileMoreAnchorEl(null);
  };

  const handleMenuClose = (link: any) => {
    if (link) history.push(link);
    setAnchorEl(null);
    handleMobileMenuClose();
  };

  const handleMobileMenuOpen = (event: React.MouseEvent<HTMLElement>) => {
    setMobileMoreAnchorEl(event.currentTarget);
  };
  const isMobileMenuOpen = Boolean(mobileMoreAnchorEl);
  const isMenuOpen = Boolean(anchorEl);

  const mobileMenuId = "primary-search-account-menu-mobile";

  const onSuccess = (response: any) => {
    props.setSnackBar({
      snackbar: {
        message: "Login Successful",
        open: true,
      },
    });
    props.setUser({ profile: response.getBasicProfile() });
  };
  const onLoginFailure = () => {
    console.log("onLoginFailure");
  };
  const onLogoutFailure = () => {
    console.log("onLogoutFailure");
  };
  const { signIn, loaded } = useGoogleLogin({
    onSuccess,
    clientId:
      "65762441475-afp9kche8th74ipqul5m94ojc8vsbjc8.apps.googleusercontent.com",
    isSignedIn: true,
    fetchBasicProfile: true,
  });

  const onLogoutSuccess = () => {
    props.setSnackBar({
      snackbar: {
        message: "Logout Successful",
        open: true,
      },
    });

    console.log("onLogoutSuccess");
    props.setUser({ profile: null });
  };
  const { signOut } = useGoogleLogout({
    clientId:
      "65762441475-afp9kche8th74ipqul5m94ojc8vsbjc8.apps.googleusercontent.com",
    onLogoutSuccess,
  });
  const handleLogout = () => {
    signOut();
    handleMenuClose(null);
  };
  const handleLogin = () => {
    signIn();
    handleMenuClose(null);
  };
  const renderMenu = (
    <Menu
      anchorEl={anchorEl}
      anchorOrigin={{ vertical: "top", horizontal: "right" }}
      id={menuId}
      keepMounted
      transformOrigin={{ vertical: "top", horizontal: "right" }}
      open={isMenuOpen}
      onClose={handleMenuClose}
    >
      {props.profile ? (
        <MenuItem onClick={() => handleMenuClose("/Profile")}>Profile</MenuItem>
      ) : (
        ""
      )}
      {props.profile ? (
        <MenuItem onClick={() => handleMenuClose("/MyAccount")}>
          My account
        </MenuItem>
      ) : (
        ""
      )}
      {props.profile ? (
        <MenuItem onClick={() => handleLogout()}>Logout</MenuItem>
      ) : (
        <MenuItem onClick={() => handleLogin()}>Login</MenuItem>
      )}
    </Menu>
  );
  const renderMobileMenu = (
    <Menu
      anchorEl={mobileMoreAnchorEl}
      anchorOrigin={{ vertical: "top", horizontal: "right" }}
      id={mobileMenuId}
      keepMounted
      transformOrigin={{ vertical: "top", horizontal: "right" }}
      open={isMobileMenuOpen}
      onClose={handleMobileMenuClose}
    >
      <MenuItem>
        <IconButton aria-label="show 4 new mails" color="inherit">
          <Badge badgeContent={4} color="secondary">
            <MailIcon />
          </Badge>
        </IconButton>
        <p>Messages</p>
      </MenuItem>
      <MenuItem>
        <IconButton aria-label="show 11 new notifications" color="inherit">
          <Badge badgeContent={11} color="secondary">
            <NotificationsIcon />
          </Badge>
        </IconButton>
        <p>Notifications</p>
      </MenuItem>
      {props.profile ? (
        <MenuItem onClick={() => handleMenuClose("/Profile")}>Profile</MenuItem>
      ) : (
        ""
      )}
      {props.profile ? (
        <MenuItem onClick={() => handleMenuClose("/MyAccount")}>
          My account
        </MenuItem>
      ) : (
        ""
      )}

      {props.profile ? (
        <MenuItem onClick={() => handleLogout()}>Logout</MenuItem>
      ) : (
        <MenuItem onClick={() => handleLogin()}>Login</MenuItem>
      )}
    </Menu>
  );
  return (
    <div className={classes.root}>
      <CssBaseline />
      <AppBar
        position="fixed"
        className={clsx(classes.appBar, {
          [classes.appBarShift]: open,
        })}
      >
        <Toolbar>
          <IconButton
            color="inherit"
            aria-label="open drawer"
            onClick={handleDrawerOpen}
            edge="start"
            className={clsx(classes.menuButton, {
              [classes.hide]: open,
            })}
          >
            <MenuIcon />
          </IconButton>
          <Typography variant="h6" noWrap>
            Dialer App
          </Typography>
          <div className={classes.grow} />
          <div className={classes.sectionDesktop}>
            {props.profile ? (
              <>
                {" "}
                <IconButton aria-label="show 4 new mails" color="inherit">
                  <Badge badgeContent={4} color="secondary">
                    <MailIcon />
                  </Badge>
                </IconButton>
                <IconButton
                  aria-label="show 17 new notifications"
                  color="inherit"
                >
                  <Badge badgeContent={17} color="secondary">
                    <NotificationsIcon />
                  </Badge>
                </IconButton>
              </>
            ) : (
              ""
            )}
            <IconButton
              edge="end"
              aria-label="account of current user"
              aria-controls={menuId}
              aria-haspopup="true"
              onClick={handleProfileMenuOpen}
              color="inherit"
            >
              {props.profile ? (
                <AvatarComponent></AvatarComponent>
              ) : (
                <AccountCircle />
              )}
            </IconButton>
          </div>
          <div className={classes.sectionMobile}>
            {/* <IconButton
              aria-label="show more"
              aria-controls={mobileMenuId}
              aria-haspopup="true"
              onClick={handleMobileMenuOpen}
              color="inherit"
            >
              <MoreIcon />
            </IconButton> */}
            <IconButton
              edge="end"
              aria-label="account of current user"
              aria-controls={menuId}
              aria-haspopup="true"
              onClick={handleProfileMenuOpen}
              color="inherit"
            >
              {props.profile ? (
                <AvatarComponent></AvatarComponent>
              ) : (
                <AccountCircle />
              )}
            </IconButton>
          </div>
        </Toolbar>
      </AppBar>
      {renderMobileMenu}
      {renderMenu}
      <Drawer
        variant="permanent"
        className={clsx(classes.drawer, {
          [classes.drawerOpen]: open,
          [classes.drawerClose]: !open,
        })}
        classes={{
          paper: clsx({
            [classes.drawerOpen]: open,
            [classes.drawerClose]: !open,
          }),
        }}
      >
        <div className={classes.toolbar}>
          <IconButton onClick={handleDrawerClose}>
            {theme.direction === "rtl" ? (
              <ChevronRightIcon />
            ) : (
              <ChevronLeftIcon />
            )}
          </IconButton>
        </div>
        <Divider />
        <List>
          {dashboardRoutes.map((route: any, index: any) => (
            <ListItem
              key={route.name}
              button
              component="a"
              onClick={() => {
                linkClick(route.path);
              }}
            >
              <ListItemIcon>{route.icon}</ListItemIcon>
              <ListItemText primary={route.name} />
            </ListItem>
          ))}
        </List>
      </Drawer>
      <main className={classes.content}>
        <div className={classes.toolbar} />
        <Container maxWidth="lg" style={{ marginTop: "20px" }}>
          <Grid item>{/* <Navbar></Navbar> */}</Grid>
          <Grid container item spacing={5} direction="row" justify="center">
            <Switch>
              {dashboardRoutes.map((route: any) => {
                return (
                  <Route path={route.path} key={route.path}>
                    {route.component}
                  </Route>
                );
              })}
              <Route path={"/Profile"} key={"/Profile"}>
                <ProfileComponent></ProfileComponent>
              </Route>
              <Route path={"/MyAccount"} key={"/MyAccount"}>
                <MyAccount></MyAccount>
              </Route>
              <Route path={"/"} key={"/Reporting"}>
                <Reporting></Reporting>
              </Route>
            </Switch>
          </Grid>
        </Container>
      </main>
    </div>
  );
}
const mapStateToProps = (state: any) => {
  return { profile: state.login.profile };
};

export default connect(mapStateToProps, { setUser, setSnackBar })(MiniDrawer);
