import Avatar from "@material-ui/core/Avatar";
import { createStyles, makeStyles, Theme } from "@material-ui/core/styles";
import React from "react";
import { connect } from "react-redux";

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    large: {
      width: theme.spacing(5),
      height: theme.spacing(5),
    },
  })
);

function AvatarComponent(props: any) {
  const classes = useStyles();
  return (
    <Avatar aria-label="profile" className={classes.large}>
      <img src={props.profile?.fI} width="100%" height="100%" />
    </Avatar>
  );
}
const mapStateToProps = (state: any) => {
  return { profile: state.login.profile };
};
export default connect(mapStateToProps)(AvatarComponent);
