// *https://www.registers.service.gov.uk/registers/country/use-the-api*
import { Grid, MenuItem, TextField } from "@material-ui/core";
import CircularProgress from "@material-ui/core/CircularProgress";
import Autocomplete from "@material-ui/lab/Autocomplete";
import React from "react";
import StockDetails from "./Cards/StockDetails";
import StockDetailsComponent from "./Profile/StockDetailsComponent";

interface StockType {
  name: string;
  exchange: string;
  index: string;
  isYahooFinance: boolean;
  longname: string;
  quoteType: string;
  score: number;
  shortname: string;
  symbol: string;
  typeDisp: string;
}

export default function StocksAutoComplete() {
  const [open, setOpen] = React.useState(false);
  const [value, setValue] = React.useState("");
  const [options, setOptions] = React.useState<StockType[]>([]);
  const [selectedStock, setSelectedStock] = React.useState<any>();
  const [currency, setCurrency] = React.useState("US");
  const [inputValue, setInputValue] = React.useState("");
  const [loading, setLoading] = React.useState(false);

  const handleChange = (event: any) => {
    setValue("");
    setOptions([]);
    setSelectedStock(null);
    setCurrency(event.target.value);
    setInputValue("");
  };
  React.useEffect(() => {
    if (!open) {
      setOptions([]);
    }
  }, [open]);
  const handleOnChange = async (event: any) => {
    setValue(event.target.value);
    if (loading || value === "") {
      return undefined;
    }
    if (value.length < 2) {
      return undefined;
    }
    setLoading(true);
    console.log(currency);
    const options = {
      method: "GET",
      hostname: "https://apidojo-yahoo-finance-v1.p.rapidapi.com",
      port: null,
      path: "/auto-complete?q=" + event.target.value + "&region=" + currency,
      headers: {
        "x-rapidapi-key": "15dd709fd1msh5ae0f6062bcf759p1a36b4jsnbabd43d64baa",
        "x-rapidapi-host": "apidojo-yahoo-finance-v1.p.rapidapi.com",
        useQueryString: true,
      },
    };
    const headers: any = {
      "x-rapidapi-key": "15dd709fd1msh5ae0f6062bcf759p1a36b4jsnbabd43d64baa",
      "x-rapidapi-host": "apidojo-yahoo-finance-v1.p.rapidapi.com",
      useQueryString: true,
    };
    const response = await fetch(options.hostname + options.path, {
      method: options.method,
      headers,
    });
    const stocks: any = await response.json();
    setLoading(false);
    setOptions(stocks.quotes);
  };
  const currencies = [
    {
      value: "US",
      label: "USA",
      symbol: "$",
    },
    {
      value: "IN",
      label: "India",
      symbol: "",
    },
  ];

  const onInputChange = (event: any, newInputValue: any) => {
    setInputValue(newInputValue);
  };
  return (
    <Grid container spacing={3}>
      <Grid item xs={12} sm={6}>
        <Autocomplete
          id="stock-demo"
          open={open}
          onOpen={() => {
            setSelectedStock(null);
            setInputValue("");
            setOptions([]);
            setValue("");
            setOpen(true);
          }}
          onClose={() => {
            setOpen(false);
          }}
          getOptionSelected={(option, value) => {
            if (option.shortname === value.shortname) {
              setSelectedStock(option);
              return true;
            } else {
              return false;
            }
          }}
          getOptionLabel={(option) => option.shortname}
          //   renderOption={(option) => <Typography noWrap>{option}</Typography>}
          value={selectedStock}
          options={options}
          loading={loading}
          inputValue={inputValue}
          onInputChange={onInputChange}
          renderInput={(params) => (
            <TextField
              {...params}
              label="Enter The Stock Name(Atleast 3 letters)"
              variant="outlined"
              onChange={handleOnChange}
              value={value}
              InputProps={{
                ...params.InputProps,
                endAdornment: (
                  <React.Fragment>
                    {loading ? (
                      <CircularProgress color="inherit" size={20} />
                    ) : null}
                    {params.InputProps.endAdornment}
                  </React.Fragment>
                ),
              }}
            />
          )}
        />
      </Grid>
      <Grid item xs={12} sm={6}>
        <TextField
          id="outlined-select-currency"
          select
          label="Select"
          value={currency}
          defaultValue={currency}
          onChange={handleChange}
          helperText="Please select your currency"
          variant="outlined"
        >
          {currencies.map((option) => (
            <MenuItem key={option.value} value={option.value}>
              {option.label}
            </MenuItem>
          ))}
        </TextField>
      </Grid>
      <Grid>
        {selectedStock != null ? (
          <StockDetails
            symbol={selectedStock.symbol}
            shortname={selectedStock.shortname}
            score={selectedStock.score}
            currency={currency}
          ></StockDetails>
        ) : (
          ""
        )}
      </Grid>
    </Grid>
  );
}
