import AllOutIcon from "@material-ui/icons/AllOut";
import DashboardIcon from "@material-ui/icons/Dashboard";
import NoteAddIcon from "@material-ui/icons/NoteAdd";
import React from "react";
import Campaign from "./components/Campaign";
import CreateCampaignStepper from "./components/CreateCampaign";
import Dashboard from "./components/Dashboard";
import AssessmentIcon from "@material-ui/icons/Assessment";
import Reporting from "./components/Reporting";
const dashboardRoutes = [
  {
    path: "/dashboard",
    name: "Dashboard",
    icon: <DashboardIcon></DashboardIcon>,
    component: <Dashboard></Dashboard>,
    layout: "/admin",
  },
  {
    path: "/campaign",
    name: "Campaign",
    icon: <AllOutIcon></AllOutIcon>,
    component: <Campaign></Campaign>,
    layout: "/campaign",
  },
  {
    path: "/reporting",
    name: "Reporting",
    icon: <AssessmentIcon></AssessmentIcon>,
    component: <Reporting></Reporting>,
    layout: "/reporting",
  },
  {
    path: "/CreateCampaignStepper",
    name: "Create Campaign",
    icon: <NoteAddIcon></NoteAddIcon>,
    component: <CreateCampaignStepper></CreateCampaignStepper>,
    layout: "/CreateCampaignStepper",
  },
];

export default dashboardRoutes;
