import { CircularProgress, Grid } from "@material-ui/core";
import Paper from "@material-ui/core/Paper";
import { createStyles, makeStyles, Theme } from "@material-ui/core/styles";
import React from "react";
import Chart from "react-google-charts";
import ReactJson from "react-json-view";

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    root: {
      flexGrow: 1,
    },
    paper: {
      padding: theme.spacing(2),
      margin: "auto",
      maxWidth: 500,
    },
    image: {
      width: 128,
      height: 128,
    },
    img: {
      margin: "auto",
      display: "block",
      maxWidth: "100%",
      maxHeight: "100%",
    },
  })
);

export default class StockDetails extends React.Component<
  { symbol: string; shortname: string; score: string; currency: string },
  { morestockDetailsData: any; chartData: any }
> {
  constructor(props: any) {
    super(props);
    this.state = { morestockDetailsData: undefined, chartData: undefined };
  }
  componentDidMount() {
    this.fetchData(this.props.symbol, this.props.currency);
    this.fetchChartData();
  }
  componentDidUpdate(prevProps: any) {
    if (this.props.symbol !== prevProps.symbol) {
      console.log(prevProps);
      console.log("Fetch");
    }
  }
  fetchData = async (symbol: string, currency: string) => {
    const options = {
      method: "GET",
      hostname: "https://apidojo-yahoo-finance-v1.p.rapidapi.com",
      port: null,
      path: "/stock/v2/get-financials?symbol=" + symbol + "&region=" + currency,
      headers: {
        "x-rapidapi-key": "15dd709fd1msh5ae0f6062bcf759p1a36b4jsnbabd43d64baa",
        "x-rapidapi-host": "apidojo-yahoo-finance-v1.p.rapidapi.com",
        useQueryString: true,
      },
    };
    const headers: any = {
      "x-rapidapi-key": "15dd709fd1msh5ae0f6062bcf759p1a36b4jsnbabd43d64baa",
      "x-rapidapi-host": "apidojo-yahoo-finance-v1.p.rapidapi.com",
      useQueryString: true,
    };
    const response = await fetch(options.hostname + options.path, {
      method: options.method,
      headers,
    });
    const morestockDetailsData: any = await response.json();
    console.log(morestockDetailsData);
    this.setState({ morestockDetailsData: morestockDetailsData });
  };
  fetchChartData = async () => {
    const headers: any = {
      "x-rapidapi-key": "15dd709fd1msh5ae0f6062bcf759p1a36b4jsnbabd43d64baa",
      "x-rapidapi-host": "apidojo-yahoo-finance-v1.p.rapidapi.com",
      useQueryString: true,
    };
    const response = await fetch(
      "https://apidojo-yahoo-finance-v1.p.rapidapi.com/stock/v2/get-chart" +
        "?interval=1y&symbol=" +
        this.props.symbol +
        "&range=1d&region=" +
        this.props.currency,
      {
        method: "GET",
        headers,
      }
    );
    const chartData: any = await response.json();
    console.log(chartData);
    let data: any = [];
    data = [
      [
        "time",
        // "low", "high",
        "close",
      ],
      ...chartData.chart.result[0].timestamp
        .map((d: any, index: any) => [
          "" + d,
          chartData.chart.result[0].indicators.quote[0].close[index],
        ])
        .reverse(),
    ];
    this.setState({ chartData: data });
  };
  render() {
    console.log(this.state);
    return (
      <Grid container spacing={4}>
        <Grid item>
          {this.state.chartData ? (
            <Paper>
              {" "}
              <Chart
                width={700}
                height={"400px"}
                chartType="AreaChart"
                loader={<div>Loading Chart</div>}
                data={this.state.chartData}
                options={{
                  title: "Stocks",
                  hAxis: { title: "time", titleTextStyle: { color: "#333" } },
                }}
              />
            </Paper>
          ) : (
            <CircularProgress />
          )}{" "}
        </Grid>
        <Grid item>
          {this.state.morestockDetailsData ? (
            <Paper>
              <ReactJson src={this.state.morestockDetailsData} />
            </Paper>
          ) : (
            <CircularProgress />
          )}{" "}
        </Grid>
      </Grid>
    );
  }
}
